.App {
  text-align: center;
}
header{
  color: white;
  margin-bottom: 1.2rem;
}
body{
  background-color: #2b2e4a;
}
.inpfield input,button{
  font-size: 1.2rem;
  padding:10px 20px;
}
.inpfield input{
  font-size: 1.2rem;
  padding:10px 20px;
  border-radius: 30px 0px 0px 30px;
  border: 0px;
  outline: none;
}
.inpfield {
  display: flex;
  justify-content: center;
  margin: auto;
  width: 100%;
}
.inpfield button{
  border: 0;
  font-size: 1.2rem;
  border-radius: 0px 0px 0px 0px;
  outline: none;
  background-color: #e4d3cf;
}
.inpfield button:hover{
  background-color: #e84545;
  color: white;
}
.inpfield button:active{
  background-color: #903749;
  color: white;
}

ul.list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: block;
  flex-direction: row;
  list-style: none;
  margin: auto;
  margin-top: 20px;
 min-width: 200px;
 max-width: 500px;
 

}
.list li{
  display: flex;
  list-style: none;
  justify-content: space-between;
  align-items: center;
  margin: auto;
  background-color: white;
  font-size: 1.2rem;
  padding-left: 0px;
  margin-left: 0px;
  margin-top: 15px;
 
}
.list li button{
  font-size: 1.2rem;
  margin: 0;
}
.list .content{
  padding: 10px;
}
.btn .del {
  border: 0px;
  padding: 15px;
  border-radius: 0;
  outline: none;
  color: white;
  background-color: #bb8082;
}
.btn .del:hover{
  outline: none;
  background-color: #6b4648;
}
.btn .del:active{
  outline: none;
  background-color: #555151;
}
.btn .ch {
  border: 0px;
  padding: 15px;
  border-radius: 0;
  outline: none;
  color: white;
  background-color: #007580;
}
.btn .ch:hover{
  outline: none;
  background-color: #9ecca4;
}
.btn .ch:active{
  outline: none;
  background-color: #92967d;
}
.inpfield select {
  padding: 12px;
  border-radius: 2px ;
  font-size: 15px;
  margin-left: 30px;
}


